@media (max-width: 991.98px) {
  .menu__body {
    opacity: 0;
    position: fixed;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    overflow: auto;
    background-color: #fff;
    padding: 120px 20px 30px 20px;
    z-index: 2;
    transition: all .3s ease;
    visibility: hidden;
  }

  .menu__body:before {
    content: '';
    position: fixed;
    width: 100%;
    height: 105px;
    top: 0;
    left: 0;
    background-color: #009cb0;
    z-index: 3;
  }

  .menu__body.active {
    opacity: 1;
    visibility: visible;
  }

  .menu__list {
    display: block;
  }

  .menu__list li {
    margin: 0px 0px 15px 0px;
  }

  .menu__link {
    color: #000;
    padding: 0;
    font-size: 20px;
  }

  .icon-menu {
    display: block;
    position: relative;
    width: 30px;
    height: 18px;
    cursor: pointer;
    z-index: 50;
  }

  .icon-menu span {
    transition: all 0.3s ease 0s;
    top: calc(50% - 1px);
    left: 0px;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #fff;
  }

  .icon-menu span:first-child {
    top: 0px;
  }

  .icon-menu span:last-child {
    top: auto;
    bottom: 0px;
  }

  .icon-menu.active span {
    transform: scale(0);
  }

  .icon-menu.active span:first-child {
    transform: rotate(-45deg);
    top: calc(50% - 1px);
  }

  .icon-menu.active span:last-child {
    transform: rotate(45deg);
    bottom: calc(50% - 1px);
  }

  .contacts-header {
    justify-content: flex-end;
  }

  .contacts-header__item_second-phone {
    display: none;
  }

  .actions-header .actions-header__item {
    padding: 0;
    background: none;
  }

  .info-header__schedule {
    display: none;
  }
}

@media (max-width: 991.98px) and (max-width: 767.98px) {
  .menu__body {
    padding: 90px 20px 30px 20px;
